.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.mitad {
  display: block;
  padding: 1rem;
  width: 50%;
}
.pegado {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
}

.pegados {
  align-items: center;
  display: flex;
  margin: auto;
  overflow-y: hidden;
}

.contenido {
  height: 65%;
  overflow-y: scroll;
  background-color: lemonchiffon;
}
.pegados2 {
  align-items: center;
  display: flex;
  margin: auto;
  overflow-y: hidden;
}
.pegados::-webkit-scrollbar {
  display: inline;
}

.bordecito {
  align-items: center;
  padding: 2%;
  width: 30vw;
  height: 15vh;
}

.chico {
  display: block;
  height: 75%;
}

.bordecitoCarrito {
  align-items: center;
  border: 1px dashed grey;
  padding: 1rem;
  width: 40%;
  height: 200px;
}

.chicoCarrito {
  display: block;
  font-size: x-small;
  height: 50%;
}

.bajar {
  margin: auto;
}

.flotar {
  float: right;
}
.flotar2 {
  float: right;
  margin-top: 5px;
}
.floatbtn {
  position: fixed;
  width: 50px;
  height: 50px;
  bottom: 40px;
  right: 40px;
  background-color: gold;
  border: 1px grey;
  color: #fff;
  border-radius: 50px;
  text-align: center;
  box-shadow: 2px 2px 3px #999;
}

.my-float {
  margin-top: 22px;
}

.App-link {
  color: #61dafb;
}

.Prueba {
  overflow-y: auto;
  height: 75vh;
  background-color: aquamarine;
  padding: 5px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
